:root {
  --input-padding-x: 1.5rem;
  --input-padding-y: .75rem;
}

body {
  font-family: 'Josefin Sans', sans-serif;
  background: #242424;
  background: linear-gradient(to right, #5f6975, #061118);
}

.jumbotron{
  border-radius: 15px;
}

.card-addexe {
  border: 0;
  border-radius: 1rem;
  box-shadow: 0 0.5rem 1rem 0 rgba(0, 0, 0, 0.1);
  height: 35vh;
  width: 55vh;
  margin: 0 auto;
  float: none;
  margin-bottom: 10px;
  margin-top: 6%;
}

.card-signin {
  border: 0;
  border-radius: 1rem;
  box-shadow: 0 0.5rem 1rem 0 rgba(0, 0, 0, 0.1);
  height: 35vh;
}

.card-signin .card-title {
  margin-bottom: 2rem;
  font-weight: 300;
  font-size: 1.5rem;
}

.card-signin .card-img-left {
  width: 45%;
  background-size: cover;
}

.card-signin .card-body {
  padding: 2rem;
}

.form-signin {
  width: 100%;
}

.form-signin .btn {
  font-size: 80%;
  border-radius: 5rem;
  letter-spacing: .1rem;
  font-weight: bold;
  padding: 1rem;

}

.form-label-group {
  position: relative;
  margin-bottom: 1rem;
}

.form-label-group input {
  height: auto;
  border-radius: 2rem;
}

.form-label-group>input,
.form-label-group>label {
  padding: .75rem 1.5rem;
  padding: var(--input-padding-y) var(--input-padding-x);
}

.form-label-group>label {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  margin-bottom: 0;
  /* Override default `<label>` margin */
  line-height: 1.5;
  color: #495057;
  border: 1px solid transparent;
  border-radius: .25rem;
}

.card-signup {
  border: 0;
  border-radius: 1rem;
  box-shadow: 0 0.5rem 1rem 0 rgba(0, 0, 0, 0.1);
  height: 55vh;
}



.card-signup .card-img-left {
  width: 45%;
  background-size: cover;
  border-radius: 5px;
}


.exercise {
  width: 30vh;
  height: 19vh;
  border: none;
  border-radius: 15px;
  
}

.exercise .card-body{
  border-radius: 5px;
  border: none;
}

.add-exercise{
  height: 7vh;
  border: 0;
}




